<template>
  <div class="container mt-5">
    <h1>Todo List</h1>
    <div class="input-group mt-3 mb-3">
      <input
        type="text"
        class="form-control"
        placeholder="請輸入代辦事項"
        v-model="content"
      />
      <div class="input-group-append">
        <button
          class="btn btn-outline-secondary"
          type="button"
          @click="addTodo"
        >
          送出
        </button>
      </div>
    </div>
    <ul class="list-group mt-3">
      <li
        v-for="(item, id) in data"
        :key="id"
        class="list-group-item d-flex justify-content-between align-items-center"
      >
        <div>{{ item.content }}</div>
        <button type="button" class="btn btn-dark" @click="removeTodo(id)">
          刪除
        </button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      data: {},
      content: "",
      canSend: true
    };
  },
  methods: {
    getTodo() {
      this.axios.get("https://serene-forest-27769.herokuapp.com").then(res => {
        this.data = res.data.result;
      });
    },
    addTodo() {
      if (!this.canSend || this.content == "") {
        return;
      }
      this.canSend = false;
      this.axios
        .post("https://serene-forest-27769.herokuapp.com/addTodo", {
          content: this.content
        })
        .then(res => {
          this.getTodo()
          this.content = "";
          this.canSend = true;
        });
    },
    removeTodo(id) {
      this.axios
        .post("https://serene-forest-27769.herokuapp.com/removeTodo", {
          id: id
        })
        .then(res => {
          this.getTodo()
        });
    }
  },
  mounted() {
    this.getTodo();
  }
};
</script>

<style lang="scss" scoped>
@import "../node_modules/bootstrap/scss/bootstrap.scss";
* {
  font-family: "微軟正黑體";
}
</style>



